import { createSlice } from "@reduxjs/toolkit";

const profilePicSlice = createSlice({
  name: "profilePic",
  initialState: {
    imageName: null,
    imageFile: null,
    delStatus: false,
  },
  reducers: {
    setImageName: (state, acion) => {
      const { imageName } = acion.payload;
      if (state.imageName !== null) {
        state.imageName = null;
      }
      state.imageName = imageName;
    },
    setImageFile: (state, action) => {
      const { imageFile } = action.payload;
      if (state.imageFile !== null) {
        state.imageFile = null;
      }
      state.imageFile = (imageFile);
    },
    delImage: (state) => {
      state.imageFile = null;
    },
    setDelStatusToTrue: (state) =>{
      state.delStatus = true
    },
    setDelStatusToFalse: (state) =>{
      state.delStatus = false
    }
  },
});

export const { setImageFile, setImageName, delImage, setDelStatusToTrue, setDelStatusToFalse} = profilePicSlice.actions;
export default profilePicSlice.reducer; 