import React, { useEffect, useState } from "react";
import { uploadImage, getImage, deleteImage } from "../../utils/asyncCalls";
import {
  setImageFile,
  setImageName,
  delImage,
  setDelStatusToTrue,
  setDelStatusToFalse,
} from "../../Redux/profilePicSlice";
import { useSelector, useDispatch, shallowEqual } from "react-redux";
import defaultPic from "../../utils/icons/user.svg";

const ProfilePicture = () => {
  const [uploadingFile, setUploadingFile] = useState();
  const { token } = useSelector((state) => state.auth);
  const [error, setError] = useState(null);
  const dispatch = useDispatch();
  const { imageFile, delStatus } = useSelector(
    (state) => state.profilePic,
    shallowEqual
  );
  console.log(imageFile);

  console.log(delStatus);

  useEffect(() => {
    if (delStatus === false) {
      const fetchImage = async () => {
        const dataRes = await getImage(token);
        console.log(dataRes);
        return dataRes;
      };
      if (!imageFile) {
        try {
          fetchImage()
            .then((data) => dispatch(setImageFile({ imageFile: data })))
            .catch((er) => {
              if (er) {
                setError(true);
              }
            });
        } catch (error) {
          setError(error.response);
        }
      } else if(imageFile.status === 404 || imageFile.status === 500) {
         dispatch(setImageFile({imageFile: defaultPic}))
        
      }
    }
  }, []);
  console.log(error);

 

  // for uploading image
  const uploadImageHandler = async (e) => {
    e.preventDefault();

    if (imageFile) {
      try {
        await deleteImage(token, imageFile);
      } catch (error) {
        setError(error);
      }
    }

    const formData = new FormData();
    if (uploadingFile) {
      formData.append("image", uploadingFile);
    }

    try {
      const dataRes = await uploadImage(formData, token);
      console.log(dataRes.data);
      dispatch(setImageFile({ imageFile: dataRes.data }));
      dispatch(setImageName({ imageName: dataRes.data }));
      dispatch(setDelStatusToFalse());
    } catch (error) {
      setError(error);
    }
  };

  // for Deletion of Profile Picture
  const deleteImageHandler = async (e) => {
    e.preventDefault();
    if (imageFile) {
      try {
        const dataRes = await deleteImage(token, imageFile);
        if (dataRes.data.success === true) {
          dispatch(delImage());
          dispatch(setDelStatusToTrue());
        }
      } catch (error) {
        setError(error);
      }
    }
  };

  return (
    <div
      id="profile-pic-upload"
      style={{
        margin: 5 + "rem",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      {delStatus === false && !imageFile ? (
        <>
          <img
            src={defaultPic}
            alt="User-Name"
            style={{ height: 100, width: 100, borderRadius: 50 + "%" }}
          />
        </>
      ) : (
        <>
          {" "}
          <>
            <img
              src={imageFile}
              alt="User-Name"
              style={{ height: 100, width: 100, borderRadius: 50 + "%" }}
            />
          </>
        </>
      )}
      <form onSubmit={uploadImageHandler}>
        <input
          type="file"
          className="imageUpload"
          onChange={(e) => setUploadingFile(e.target.files[0])}
        />
        <button type="submit">Upload</button>
        <button onClick={deleteImageHandler} style={{ margin: "0 .5rem" }}>
          Delete
        </button>
      </form>
    </div>
  );
};

export default ProfilePicture;
