import React, { memo, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import appImage from "./app-face-logo.png";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { useJwtTokenChecker } from "../../utils/jwtTokenChecker";
import { getImage } from "../../utils/asyncCalls";
import { setImageFile } from "../../Redux/profilePicSlice";
import defaultPic from "../../utils/icons/user.svg";

const Header = memo(() => {
  const { user, token } = useSelector((state) => state.auth, shallowEqual);
  console.log(token);
  console.log(user);

  const { jwtTokenChecker } = useJwtTokenChecker();
  const { cartItem } = useSelector((state) => state.cart);
  const { wishlistPage } = useSelector((state) => state.wish);
  const { imageFile, imageName, delStatus } = useSelector(
    (state) => state.profilePic,
    shallowEqual
  );
  console.log(imageFile, `Here is the image`, imageName);
  const dispatch = useDispatch();
  const [error, setError] = useState();
  const [defPic, setDefPic] = useState(null);

  // for User Presence
  useEffect(() => {
    jwtTokenChecker();
  }, []);



  useEffect(() => {
    if (delStatus === false) {
      const fetchImage = async () => {
        const dataRes = await getImage(token);
        console.log(dataRes.error);
        console.log(dataRes.data)
        return dataRes;
      };
      if (!imageFile) {
        try {
          fetchImage()
            .then((data) => dispatch(setImageFile({ imageFile: data })))
            .catch((er) => {
              if (er) {
                setError(true);
              }
            });
        } catch (error) {
          setError(error.response);
        }
      } else if(imageFile.status === 404 || imageFile.status === 500) {
         dispatch(setImageFile({imageFile: defaultPic}))
        
      }
    } else {
      setDefPic(defaultPic);
    }
  }, []);


  

  console.log(error);

  return (
    <header>
      {user ? (
        <>
          <div className="header-container">
            <div className="header-left">
              <Link to="/profile-picture">
                <div className="user-name-container">
                  { delStatus === false &&  imageFile  ? (
                    <>
                      <img
                        src={imageFile}
                        alt="pic"
                        accept="image/*"
                        className="user-image"
                      />
                    </>
                  ) : (
                    <>
                      <img src={defaultPic} alt="pic" className="user-image" />
                    </>
                  ) }

                  <div className="user-name">
                    <p>
                      {" "}
                      {user.firstNm.charAt(0).toUpperCase() +
                        user.firstNm.slice(1)}
                    </p>
                  </div>
                </div>
              </Link>
              <div className="app-name-container">
                <h1>Ultronix </h1>
                <span>Mobile-Store</span>
              </div>
            </div>

            <nav className="nav-links">
              <Link to="/" className="link">
                <div id="home-icon" />
                <span>Home</span>
              </Link>
              <Link to="/product" className="link">
                <div id="product-icon" />
                <span>Products</span>
              </Link>
              <Link to="/profile" className="link">
                <div id="profile-icon" />
                <span>Profile</span>
              </Link>
              <Link to="/about" className="link">
                <div id="about-icon" />
                <span>About</span>
              </Link>
              <Link to="/order" className="link">
                <div id="order-icon" />
                <span>Orders</span>
              </Link>
              <Link to="/cart" className="link">
                <div id="cart-icon" />

                <span>Cart:({cartItem.length})</span>
              </Link>
              <Link to="/contact" className="link">
                <div id="contact-icon" />
                <span>Contact</span>
              </Link>
              <Link to="/wishlist" className="link">
                <div id="wishlist-icon" />
                <span>Wishlist:({wishlistPage.length})</span>
              </Link>
              <Link to="/logout" className="link">
                <div id="logout-icon" />
                <span>logout</span>
              </Link>
            </nav>
          </div>
        </>
      ) : (
        <>
          <div className="header-container">
            <div className="header-left">
             

              <div className="app-name-container">
                <h1>Ultronix </h1>
                <span>Mobile-Store</span>
              </div>
            </div>

            <nav className="nav-links">
              <Link to="/" className="link">
                <div id="home-icon" />
                <span>Home</span>
              </Link>
              <Link to="/about" className="link">
                <div id="about-icon" />
                <span>About</span>
              </Link>

              <Link to="/signup" className="link">
                <div id="signup-icon" />
                <span>Signup</span>
              </Link>
              <Link to="/login" className="link">
                <div id="login-icon" />
                <span>Login</span>
              </Link>
              <Link to="/contact" className="link">
                <div id="contact-icon" />
                <span>Contact</span>
              </Link>
            </nav>
          </div>
        </>
      )}
    </header>
  );
});

export default Header;
